<template>
    <v-card
        elevation="2"
    >
        <v-card-text>
            <v-dialog
                @click:outside="onClickOutside"
                v-model="dialog"
                persistent
                max-width="600px"
            >
                <template v-slot:activator = "{ on }">
                    <v-btn v-show="false" v-on="on"></v-btn>
                    <v-container fluid>
                        <v-row>
                            <v-col>
                                <v-text-field
                                    :placeholder="postPlaceholder"
                                    filled
                                    dense
                                    rounded
                                    class="status-field"
                                    readonly
                                    hide-details
                                    @click="openNewStatusWindow"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                        <v-row>
                            <v-col cols="4">
                                <v-btn text>
                                    <v-icon>mdi-call-made</v-icon>
                                    <span class="ml-1">ATTACH SHORTCUT</span>
                                </v-btn>
                            </v-col>
                            <v-col cols="4">
                                <v-btn text>
                                    <v-icon>mdi-file-document</v-icon>
                                    <span class="ml-1">ATTACH DOCUMENT</span>
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-container>
                </template>
                <v-card>
                    <v-card-title>
                        <v-container fluid>
                            <v-row>
                                <v-col></v-col>
                                <v-col class="d-flex justify-center"><span>Create post</span></v-col>
                                <v-col>
                                    <v-btn 
                                      icon
                                      class="float-right"
                                      @click="close"
                                     >
                                      <v-icon>mdi-close</v-icon>
                                    </v-btn>
                                </v-col>
                            </v-row>
                        </v-container>      
                    </v-card-title>
                    <v-divider class="separator-fix"></v-divider>
                    <v-card-text>
                       <v-container fluid>
                           <v-textarea
                              v-model="post"
                              counter
                              autofocus
                              flat
                              solo
                              rows="5"                        
                              label=""
                              :rules="rules"
                            ></v-textarea>
                       </v-container>
                    </v-card-text>
                    <v-card-actions>
                        <v-btn block color="primary" @click="createNewPost">Post</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>
        </v-card-text>
    </v-card>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    name: 'Status',
    computed: {
     ...mapGetters('profile', [
       'user'
     ]),
      postPlaceholder: function(){
          return `What's on your mind, ${this.user.firstName}`
      }
    },
    data: () => ({
       dialog: false,
       rules: [v => v.length <= 1000 || 'Max 1000 characters'],
       post: ''
    }),
    methods: {
        ...mapActions('newsFeed', [
            'createPost'
        ]),
        openNewStatusWindow: function() {
            this.dialog = true;
        },
        close: function(){
            this.post = '';
            this.dialog = false
        },
        onClickOutside: function(){
            this.close();
        },
        createNewPost: function(){
            if(this.post){
                this.createPost(this.post);
                this.dialog = false;
            }
        }
    }
}
</script>

<style lang="scss">
    //not working with scoped ? find out later reason. 
    .status-field{
        input{
          cursor: pointer;
        }
    }
    .separator-fix{
        margin-top: -10px !important;
    }  
</style>